.front-page {
  --content-width: 1350px;

  article {
    // keeps flair images in the right place
    position: relative;
  }

}

.content-wrap {
  // aligns content on page
  margin-right: 6vw;
  margin-left: 6vw;

  @include bp(xl) {
    margin-right: auto;
    margin-left: auto;
    max-width: var(--content-width);
  }

  ul {
    padding-left: 0;
  }
}
