.cta-section {
  position: relative;
  background-image: url('/themes/custom/typhoon/images/parks-library-staircase.jpg');
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  background-attachment: fixed;
}

.cta-content {
}

.cta-text {
}

.cta-card--stack {
}

.cta-card {
  box-shadow: $vert-dark-3;
  z-index: 2;

  &:hover {
    box-shadow: $vert-dark-4;
    z-index: 3;
  }

  &:not(:last-child) {
    margin-bottom: $space-xl;
  }

  @include bp(sm) {
    width: rem(264);

    &:not(:last-child) {
      margin-bottom: $space-2xl;
    }
  }
}

.cta-card--image {
  padding-top: percentage(202/280);

  img {
    z-index: 1;
  }

  figcaption {
    position: absolute;
    left: 0;
    bottom: $space-lg;
    background: linear-gradient(135deg, $accent-color 86%, transparent 0) 100%;
    font-family: $merriweather_stack;
    width: 80%;
    line-height: 1.3;
    z-index: 2;
  }
}
